body{
	margin: 0 0 0 0;
	padding: 0 0 0 0;
}

button{
	width: 200px;
	height: 40px;
	color: white;
	border: none;
	cursor: pointer;
	margin-left: auto;
    margin-right: auto;
	margin-top: 30px;
	border-radius: 5px;
	outline: none;
}

.building_form{
	width: 400px;
	height: 220px;
	margin-left: auto;
    margin-right: auto;
	margin-top: 30px;
	display: block;
	border-top: 1px solid #E8E8E8;
	border-bottom: 1px solid #E8E8E8;
}

.building_form_input{
	display: flex;
	background-color: ;
	width: 350px;
	height: 30px;
	margin: 30px 0 30px 25px;
}

p{
	margin: 3px 0 0 0;
	font-family: system-ui;
}

input[type=number]{
	border: 1px solid #E8E8E8;
	margin-left: auto;
	margin-right: 0;
	display: block;
	width: 250px;
	outline: none;
}

.final_form{
	width: 400px;
	margin-left: auto;
    margin-right: auto;
	margin-top: 30px;
	display: block;
	border-top: 1px solid #E8E8E8;
	border-bottom: 1px solid #E8E8E8;
}

.final_form input{
	border: 1px solid #E8E8E8;
	margin: 30px 0 0 25px;
	display: block;
	width: 350px;
	height: 30px;
	outline: none;
}

.final_form textarea{
	margin: 30px 0 0px 25px;
	width: 350px;
	height: 100px;
	border: 1px solid #E8E8E8;
	outline: 0;
}

.checkbox_string{
	display: flex;
}

.checkbox_string label{
	margin: 28px 0 0 10px;
}

input[type=checkbox]{
	margin: 30px 0 30px 25px;
	width: 20px;
	height: 20px;
	outline: none;
}

#form_button{
	background-color: #295496;
	display: block;
}

#build_button{
	background-color: #289FDB;
	margin-bottom: 60px;
	display: block;
}

#back_button{
	background-color: #6FBCE3;
	margin-bottom: 60px;
	display: block;
}

#building_form{
	display: block;
}

#final_form{
	display: block;
